@import "./../scss/config/variables";

.square {
    width: 100px;
    height: 100px;
    background-color: $bg-secondary;
    margin: 5px;
    transition: all .2s;
    border-radius: 0%;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);

    @include breakpoint (md) {
        width: 120px;
        height: 120px;
    }
}

.square__X {
    background-color: $bg-X;
    clip-path: polygon(20% 0%, 0% 20%, 30% 50%, 0% 80%, 20% 100%, 50% 70%, 80% 100%, 100% 80%, 70% 50%, 100% 20%, 80% 0%, 50% 30%);
}

.square__O {
    background-color: $bg-O;
    border-radius: 50%;
}

.square__X.square__winner {
    animation: X-winner-animation infinite 1.5s ease-in-out;
}

.square__O.square__winner {
    animation: O-winner-animation infinite .5s ease-in-out;
}

@keyframes X-winner-animation {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@keyframes O-winner-animation {
    0% {
        border-radius: 50%;
    }
    50% {
        border-radius: 0%;
    }
    100% {
        border-radius: 50%;
    }
}