@import url('https://fonts.googleapis.com/css2?family=Shizuru&display=swap');

//Font

$Shizuru: 'Shizuru', cursive;

//colors

$bg-primary: #0A1F1F;
$bg-secondary: #BACFE8;
$bg-X: #B83D6C;
$bg-O: #88ABD7;
$dark: #080703;
$light: #F1F2D9;

//spacing

$spacing-md: 16px;
$spacing-lg: 32px;

//border raidus

$borderRadius: 12px;

//responsive design

@mixin breakpoint($point) {
    @if $point == md {
        // 768 px
        @media (min-width: 48em) {
            @content;
        }
    }
}