@import "./../scss/config/variables";

.scoreboard {
    width: 320px;
    display: grid;
    grid-auto-flow: column;
    margin: 15px 0px;

    @include breakpoint(md) {
        width: 470px;
    }

    & div {
        color: $light;
        text-align: center;
        font-size: 2em;
        padding: 5px;
        font-weight: bold;
    }

    &__X {
        background-color: $bg-X;
    }

    &__O {
        background-color: $bg-O;
    }
}